.each-slide > div {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  background-size: cover;
  background-position: center;
  height: 80vh;
}

.each-slide span {
  visibility: hidden;
  padding: 20px;
  font-size: 20px;
  background: #efefef;
  text-align: center;
}
